<template>
  <div class="face-info-body">
    <dth-header></dth-header>
    <div class="doInterval-action" v-show="isDoInterval">
      <span class="num">{{ backNum }}</span>秒后自动跳转到首页...
    </div>
    <dth-back-top></dth-back-top>
    <dth-back-home></dth-back-home>
    <dth-flow-node>
      <li>
        <div class="flow-node0 overIdCard item"></div>
        <span class="flow-text">身份证识别</span>
      </li>
      <li class="diliver over"></li>
      <li>
        <div class="flow-node1 compareAuth item"></div>
        <span class="flow-text">人证对比</span>
      </li>
      <li class="diliver current"></li>
      <li>
        <div class="flow-node2 unInvoice item"></div>
        <span class="flow-text">开票二维码识别</span>
      </li>
      <li class="diliver next"></li>
      <li>
        <div class="flow-node3 unPrint item"></div>
        <span class="flow-text">发票打印</span>
      </li>
    </dth-flow-node>
    <h5>正在进行人证对比，请摘掉帽子，露出五官，眼睛直视摄像头</h5>
    <div class="faceInfo">
      
      <!--<span class="intervalNum">{{ intervalNum }}</span>-->
      <div class="face-body">
        <img v-show="isFaceImg" :src="faceUrl" class="faceImg" />
        <video
          id="video"
          class="video"
          width="502"
          height="377"
          v-show="isMedias"
          autoplay="autoplay"
        ></video>
        <div v-show="isContent" class="cardStatus card-status-content">{{ cardReadStatus }}</div>
        <canvas id="canvas" class="canvas" v-show="isCanvasMedias" width="502" height="377"></canvas>
        <el-dialog
          :title="faceTitle"
          :visible.sync="isFaceShow"
          width="420px"
          :before-close="handleAddClose"
          :close-on-click-modal="false"
          custom-class="radius-active"
        >
          <el-row>
            <el-button type="primary" @click="goMakeInvoice">下一步</el-button>
          </el-row>
        </el-dialog>
        <audio v-show="visible" id="takeCarame" controls="controls" autoplay>
          <source src="../assets/takeCarama.mp3" type="audio/mpeg" />
        </audio>
        <div>
          <button v-show="visible" class="capture" id="capture">拍照</button>
        </div>
      </div>
      <div class="repeatReadCard" @click="reloadCurrentPage" v-show="repeatReadCard">重新开始人证对比</div>
    </div>
    
    <dth-footer></dth-footer>
  </div>
</template>
<script>
import {
  doOCRUpload /*, getAccessToken, getOCRAessioncode, postOCRBioassay*/
} from "@/api/user";
//   import html2canvas from "html2canvas"
const MediaStreamRecorder = require("msr");
import { Message } from "element-ui";
import $ from 'jquery';
import { getStorage, doInterval } from "@/utils/auth";
import DthHeader from "@/components/DthHeader/DthHeader.vue";
import DthFooter from "@/components/DthFooter/DthFooter.vue";
import DthBackHome from "@/components/DthBackHome/DthBackHome.vue";
import DthBackTop from "@/components/DthBackTop/DthBackTop.vue";
import DthFlowNode from "@/components/DthFlowNode/DthFlowNode.vue";
export default {
  name: "FaceInfo",
  components: { DthHeader, DthFooter, DthBackHome, DthBackTop, DthFlowNode },
  data() {
    return {
      visible: false,
      isFaceShow: false,
      faceTitle: "",
      intervalNum: 0,
      timer: null,
      isCarama: false,
      repeatReadCard: false,
      isMedias: false,
      isCanvasMedias: false,
      isContent: false,
      isFaceImg: true,
      cardReadStatus: "活体检测中，请稍等！",
      faceUrl: require('../assets/renzheng1.png'),
      backNum: 180,
      loopTimer: null,
      isDoInterval: false,
      doIntervalTimer: null,
      aa: "",
      mediaRecorder: null,
      access_token: ""
    };
  },
  mounted() {
    const _self = this;
    clearInterval(this.timer);
    this.timer = setInterval(() => {
      this.intervalNum++;
      if (this.intervalNum === 10) {
        clearInterval(this.timer);
        this.intervalNum = 0;
        document.getElementById("capture").click();
      }
      if (this.intervalNum === 2) {
        this.isContent = true;
        this.isMedias = true;
        this.isCanvasMedias = false;
        this.isFaceImg = false;
        this.cardReadStatus = '活体检测启动！'
      }
      if (this.intervalNum === 5) {
        this.cardReadStatus = '活体检测中！'
      }
    }, 1000);
    this.$nextTick(() => {
      const element = document.documentElement;
      if (element.requestFullscreen) {
        element.requestFullscreen();
      } else if (element.mozRequestFullScreen) {
        element.mozRequestFullScreen();
      } else if (element.webkitRequestFullscreen) {
        element.webkitRequestFullscreen();
      } else if (element.msRequestFullscreen) {
        element.msRequestFullscreen();
      }
    });
    let video = document.getElementById("video");
    if (navigator.mediaDevices === undefined) {
      navigator.mediaDevices = {};
    }

    if (navigator.mediaDevices.getUserMedia === undefined) {
      navigator.mediaDevices.getUserMedia = function(constraints) {
        let getUserMedia =
          navigator.webkitGetUserMedia || navigator.mozGetUserMedia;
        if (!getUserMedia) {
          return Promise.reject(
            new Error("getUserMedia is not implemented in this browser")
          );
        }

        return new Promise(function(resolve, reject) {
          getUserMedia.call(navigator, constraints, resolve, reject);
        });
      };
    }

    let constraints = { video: true };
    navigator.mediaDevices
      .getUserMedia(constraints)
      .then(function(stream) {
        if ("srcObject" in video) {
          video.srcObject = stream;
        } else {
          video.src = window.URL.createObjectURL(stream);
        }
        video.onloadedmetadata = function() {
          video.play();
        };

        _self.mediaRecorder = new MediaStreamRecorder(stream);
        _self.mediaRecorder.mimeType = "video/mp4";

        //  停止录像以后的回调函数

        _self.mediaRecorder.ondataavailable = blob => {
          // POST/PUT "Blob" using FormData/XHR2
          // var blobURL = URL.createObjectURL(blob);
          // console.log(blob);
          // document.write('<a href="' + blobURL + '">' + blobURL + '</a>');
          //  停止以后调用上传
          if (_self.aa == "") {
            _self.uploadToPHPServer(blob);
            _self.aa = blob;
          }
        };
        _self.mediaRecorder.start();
      })
      .catch(function(err) {
        console.log(err.name + ": " + err.message);
      });

    this.$nextTick(() => {
      this.init();
    });
    document.addEventListener("keypress", this.resetTimer, false);
    document.addEventListener("keyup", this.resetTimer, false);
    document.addEventListener("keydown", this.resetTimer, false);
    document.addEventListener("click", this.resetTimer, false);
    clearInterval(this.loopTimer);
    this.loopTimer = setInterval(() => {
      this.doInterval("doInvoice");
    }, 1000);

    this.access_token = this.getAccessToken();
    this.getOCRAessioncode(this.access_token);
  },
  destroyed() {
    clearInterval(this.loopTimer);
  },
  methods: {
    async getAccessToken() {
      // return await getAccessToken();
    },
    async getOCRAessioncode(access_token) {
      console.log(access_token);
    },
    async postOCRBioassay() {},
    blobToBase64(file, cb) {
      let reader = new FileReader();
      console.dir(file);
      reader.onload = e => {
        let base64 = e.target.result;
        cb(base64);
      };
      reader.readAsDataURL(file);
    },
    uploadToPHPServer(blob) {
      let file = new File(
        [blob],
        "msr-" + new Date().toISOString().replace(/:|\./g, "-") + ".mp4",
        {
          type: "video/mp4"
        }
      );
      // let url = URL.createObjectURL(file)
      this.blobToBase64(file, base64Url => {
        console.log(base64Url);
      });
      let formData = new FormData();
      formData.append("video-filename", file.name);
      formData.append("video-blob", file);
      console.log(formData);
      this.makeXMLHttpRequest(
        "https:/你的地址/data/save.php",
        formData,
        function() {
          let downloadURL = "https://你的地址" + file.name;
          console.log("File uploaded to this path:", downloadURL);
        }
      );
    },
    makeXMLHttpRequest(url, data, callback) {
      var request = new XMLHttpRequest();
      request.onreadystatechange = function() {
        if (request.readyState == 4 && request.status == 200) {
          callback();
        }
      };
      request.open("POST", url);
      request.send(data);
    },
    resetTimer() {
      this.backNum = 180;
      clearInterval(this.loopTimer);
      this.loopTimer = setInterval(() => {
        this.doInterval("doInvoice");
      }, 1000);
    },
    reloadCurrentPage() {
      location.reload(true);
    },
    backHome() {
      this.$router.push({ path: "/doInvoice" });
    },
    doInterval: doInterval,
    init() {
      const _self = this;
      let video = document.getElementById("video");
      let canvas = document.getElementById("canvas");
      let context = canvas.getContext("2d");
      document.getElementById("capture").addEventListener("click", () => {
        _self.mediaRecorder.stop();
        context.drawImage(video, 0, 0, video.width, video.height);
        $('.cardStatus').removeClass('card-status-content').addClass('card-active')
        this.isMedias = false;
        this.isCanvasMedias = false;
        this.isFaceImg = true;
        this.cardReadStatus = "人像比对中，请稍等！";

        // html2canvas(canvas).then(resp => {
        /*
                    console.log(resp)
                    let file = document.getElementById('file').files[0]
                    const formData = new FormData()
                    formData.append('files', file)
                    formData.append('userName', '朱小龙')
                    formData.append('idNumb', '362426198807233514')
                    doOCRUpload(formData).then(resp => {
                         console.log(resp)
                         const { error_msg } = resp.data;
                         const { score } = resp.data.result ? resp.data.result : { score: 0 };
                         if (error_msg === "SUCCESS" && score >= 60) {
                             Message({
                                    message: "身份验证通过！",
                                    type: "success",
                                    duration: 3 * 1000,
                                    offset: 200
                                });
                             setTimeout(() => {
                                 this.isFaceShow = true;
                                 this.faceTitle = "身份验证成功"
                             }, 2000)
                         } else {
                             if (error_msg === "SUCCESS" && score < 60) {
                                 Message({
                                    message: "身份测试阀值过低，请重新拍照！",
                                    type: "error",
                                    duration: 3 * 1000,
                                    offset: 200
                                });
                             } else if (error_msg !== 'SUCCESS') {
                                 Message({
                                    message: '您上传的照片模糊或检测不到人脸，请重新上传！',
                                    type: "error",
                                    duration: 3 * 1000,
                                    offset: 200
                                });
                             }
                         }
                    })
                    */
        let base64Data = canvas.toDataURL();
        let fileImg = this.base64ToBlob(base64Data);
        let formData = new FormData();
        const { userName, idNumb } = getStorage("cardInfo");
        formData.append("files", fileImg);
        formData.append("userName", `${userName}`);
        formData.append("idNumb", `${idNumb}`);
        // formData.append('userName', '朱小龙')
        // formData.append('idNumb', '362426198807233514')
        doOCRUpload(formData).then(resp => {
          const { error_msg } = resp.data;
          const { score } = resp.data.result ? resp.data.result : { score: 0 };
          if (error_msg === "SUCCESS" && score >= 60) {
            this.cardReadStatus = "人证比对成功！";
            this.faceUrl = require('../assets/renzhengDone1.png');
            this.isContent = false;
            Message({
              message: "身份验证通过！",
              type: "success",
              duration: 3 * 1000,
              offset: 200
            });
            setTimeout(() => {
              this.$router.push({ name: "makeInvoice" });
            }, 5000);
          } else {
            // context.clearRect(0, 0, video.width, video.height);
            if (error_msg === "SUCCESS" && score < 60) {
              this.cardReadStatus = "人证比对失败！";
              this.repeatReadCard = true;
              this.isContent = false;
              this.faceUrl = require('../assets/renzhengFailed1.png');
              Message({
                message: "人证对比不成功，请确保人证信息一致！",
                type: "error",
                duration: 3 * 1000,
                offset: 200
              });
              /*
                clearInterval(this.timer);
                this.timer = setInterval(() => {
                  this.intervalNum++;
                  if (this.intervalNum === 8) {
                    clearInterval(this.timer);
                    this.intervalNum = 0;
                    document.getElementById("capture").click();
                  }
                }, 1000);*/
            } else if (error_msg !== "SUCCESS") {
              this.cardReadStatus = "人证比对失败！";
              this.repeatReadCard = true;
              this.isContent = false;
              this.faceUrl = require('../assets/renzhengFailed1.png');
              Message({
                message: "请上传清晰的人脸照片！",
                type: "error",
                duration: 3 * 1000,
                offset: 200
              });
              /*clearInterval(this.timer);
                this.timer = setInterval(() => {
                  this.intervalNum++;
                  if (this.intervalNum === 8) {
                    clearInterval(this.timer);
                    this.intervalNum = 0;
                    document.getElementById("capture").click();
                  }
                }, 1000);*/
            }
          }
        });
        //});
      });
    },
    handleAddClose() {
      this.faceTitle = "";
      this.isFaceShow = false;
    },
    goMakeInvoice() {
      setTimeout(() => {
        this.$router.push({ name: "makeInvoice" });
        this.faceTitle = "";
        this.isFaceShow = false;
      }, 2000);
    },
    base64ToBlob(urlData) {
      let arr = urlData.split(",");
      let mime = arr[0].match(/:(.*?);/)[1] || "image/png";
      let bytes = window.atob(arr[1]);
      let ab = new ArrayBuffer(bytes.length);
      let ia = new Uint8Array(ab);

      for (let i = 0; i < bytes.length; i++) {
        ia[i] = bytes.charCodeAt(i);
      }

      return new Blob([ab], {
        type: mime
      });
    }
  }
};
</script>
<style lang="scss">
.face-info-body {
  height: 100%;
  height: 100%;
  overflow: hidden;
  > h5 {
    margin-bottom: 28px;
    color: #224c88;
    font-size: 1.4em;
  }
}
.repeatReadCard {
  cursor: pointer;
  width: 100%;
  margin: 10px auto 20px auto;
  text-align: center;
  padding: 15px 0;
  background-color: #fdd41b;
  border-radius: 6px;
  font-weight: bold;
  font-size: 1.3em;
  width: 502px;
}
.card-active {
  position: absolute;
  top: 0;
  left: 0;
  width: 502px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, .8);
  color: #FFD200;
  font-weight: bold;
  font-size: 1.4em;
  border-radius: 16px;
}
.card-status-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 502px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url(../assets/head.png);
  background-position: center;
  background-repeat: no-repeat;
  color: #FFD200;
  font-weight: bold;
  font-size: 1.4em;
  border-radius: 16px;
}
.doInterval-action {
  position: absolute;
  width: 58%;
  text-align: center;
  padding: 15px 0;
  top: 0;
  background-color: #e4cc1f;
  color: #fff;
  font-size: 20px;
  margin: 0 auto;
  left: 50%;
  margin-left: -29%;
  top: 110px;
  border-radius: 0px;
  .num {
    font-size: 32px;
    vertical-align: middle;
    display: inline-block;
    margin-right: 5px;
  }
}
.backHome {
  width: 240px;
  text-align: center;
  padding: 13px 0;
  background-color: #0b0b0b;
  color: #fff;
  position: absolute;
  bottom: 80px;
  left: 100px;
  cursor: pointer;
  border-radius: 20px;
  font-size: 2em;
}
.backTop {
  width: 320px;
  padding-left: 20px;
  text-align: left;
  background-color: #fff;
  padding: 35px 0 36px 0;
  text-align: center;
  color: #fff;
  display: block;
  position: absolute;
  left: 0;
  top: 110px;
  cursor: pointer;
  border-radius: 0 25px 25px 0;
  box-shadow: 1px 3px 5px #b6aead;
}
.backTop:after {
  display: flex;
  position: absolute;
  height: 67px;
  width: 316px;
  content: "打印发票";
  border-radius: 0px;
  top: 2px;
  left: 0;
  background-image: linear-gradient(to right, #ec9398, #d72028, #ec9398);
  border-radius: 0 25px 25px 0;
  align-items: center;
  justify-content: center;
  font-size: 2em;
}

.video {
  width: 100%;
  border-radius: 16px;
}
.canvas {
  width: 100%;
  border: 0;
}
.faceInfo {
  width: 502px;
  margin: 0 auto;
  position: relative;
  .face-body {
    width: 502px;
    height: auto;
    margin: 0 auto;
    position: relative;
    .faceImg {
      box-shadow: 2px 2px 5px #dedede;
    border-radius: 16px;
    }
  }
  .intervalNum {
    position: absolute;
    left: -190px;
    top: 30px;
    display: block;
    width: 170px;
    height: 170px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    background-color: #e6e5e5;
    border-radius: 50%;
    color: #1e85d8;
    font-size: 102px;
  }
}
.capture {
  padding: 8px 28px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: 0 1px 4px #ccc;
  margin: 20px 0;
  background-color: #146aaf;
  color: #fff;
  cursor: pointer;
  outline: none;
}
</style>